<template>
	<view class="register">
		<view class="topbox">
			<u-line-progress :striped="true" active-color="#448AEB" :percent="show==1?25:show==2?75:100"
				:striped-active="true" height="18" :show-percent="false"></u-line-progress>
			<view class="txt01">
				<u-icon name="checkmark-circle-fill" style="color: #448AEB;font-size: 32rpx;"></u-icon>
				<p class="actblue">基本信息</p>
			</view>
			<view class="txt01 txt02">
				<u-icon name="checkmark-circle-fill" :class="show>1?'nogray actblue':'nogray'"></u-icon>
				<p :class="show>1?'nomor actblue':'nomor'">活动分工</p>
			</view>
			<view class="txt03">
				<u-icon name="checkmark-circle-fill" :class="show>2?'nogray actblue':'nogray'"></u-icon>
				<p :class="show>2?'nomor actblue':'nomor'">经费预算</p>
			</view>
		</view>
		<u-form :model="form" ref="uForm" :error-type="errorType">
			<view class="formBox" v-show="show==1">
				<view class="mititle">
					<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
					基本信息
				</view>
				<u-form-item label="活动名称" prop="universalAntiAboutMain26" required label-width='140'>
					<u-input v-model="form.universalAntiAboutMain26" input-align="right" placeholder="请输入活动名称" />
				</u-form-item>
				<u-form-item label="活动标语" prop="title" required label-width='140'>
					<u-input v-model="form.title" input-align="right" placeholder="请输入活动标语" />
				</u-form-item>
				<u-form-item label="活动时间" prop="startDate" required right-icon="arrow-right" label-width='140'>
					<u-input v-model="form.startDate" input-align="right" :disabled="true" placeholder="请选择"
						@click="timeshow=true" />
				</u-form-item>
				<u-form-item label="活动地点" prop="address" required label-width='140'>
					<u-input v-model="form.address" input-align="right" placeholder="输入详细地址如道路、门牌号、小区、楼栋号、单元室" />
				</u-form-item>
				<u-form-item label="招募人数" prop="numberPeople" required label-width='140'>
					<u-input v-model="form.numberPeople" input-align="right" placeholder="请输入招募人数" />
				</u-form-item>
				<u-form-item label="报名截止时间" prop="deadlineDate" right-icon="arrow-right" required label-width='220'>
					<u-input v-model="form.deadlineDate" input-align="right" :disabled="true" placeholder="请选择"
						@click="timeshow1=true" />
				</u-form-item>
				<u-form-item label="报名联系人" prop="contacts" label-width='160'>
					<u-input v-model="form.contacts" input-align="right" placeholder="请输入报名联系人" />
				</u-form-item>
				<u-form-item label="联系电话" prop="contactsPhone" label-width='140'>
					<u-input v-model="form.contactsPhone" input-align="right" placeholder="请输入联系电话" />
				</u-form-item>
				<u-form-item label="活动内容" label-position="top" prop="activityContent" required label-width='140'>
					<u-input v-model="form.activityContent" type="textarea" :border="true" height="237"
						maxlength="200" />
				</u-form-item>
				<u-button @click="show=2" type="primary" style="margin-top: 20rpx;">下一步</u-button>
			</view>
			<view class="formBox" v-show="show==2">
				<view class="mititle">
					<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
					活动分工
				</view>
				<view v-for="(item,index) in form.universal_anti_about_workList" :key="index">
					<u-form-item label="分组名称" prop="activitiyName" required label-width='140'>
						<u-input v-model="item.activitiyName" input-align="right" placeholder="请输入分组名称" />
					</u-form-item>
					<u-form-item label="人数" prop="universalAntiAboutWorkMain18" required label-width='140'>
						<u-input v-model="item.universalAntiAboutWorkMain18" input-align="right"
							placeholder="请输入1组人数" />
					</u-form-item>
					<u-form-item label="具体描述" label-position="top" prop="specificActivities" required label-width='140'>
						<u-input v-model="item.specificActivities" type="textarea" :border="true" height="237"
							maxlength="200" />
					</u-form-item>
				</view>
				<u-row gutter="25">
					<u-col span="6"><u-button @click="show=1" style="margin-top: 20rpx;">上一步</u-button></u-col>
					<u-col span="6"><u-button @click="show=3" type="primary"
							style="margin-top: 20rpx;">下一步</u-button></u-col>
				</u-row>
			</view>
			<view class="formBox" v-show="show==3">
				<view class="mititle">
					<image style="width: 26rpx;margin-right: 12rpx;" mode="widthFix" src="/static/img/timi.png"></image>
					经费预算
				</view>
				<view v-for="(item,index) in form.universal_anti_about_fundsList" :key="index">
					<u-form-item label="项目名" prop="project" label-width='260'>
						<u-input v-model="form.project" input-align="right" />
					</u-form-item>
					<u-form-item label="项目金额" prop="cost" label-width='260'>
						<u-input v-model="form.cost" input-align="right" />
					</u-form-item>
				</view>
				<u-row gutter="25">
					<u-col span="6"><u-button @click="show=2" style="margin-top: 20rpx;">上一步</u-button></u-col>
					<u-col span="6"><u-button @click="submit" type="primary"
							style="margin-top: 20rpx;">提交</u-button></u-col>
				</u-row>
			</view>
		</u-form>
		<u-picker mode="time" v-model="timeshow" @confirm="time_confirm" :params="params"></u-picker>
		<u-picker mode="time" v-model="timeshow1" @confirm="time_confirm1" :params="params"></u-picker>
		<u-toast ref="uToast"></u-toast>
	</view>
</template>

<script>
	import {
		MyUniversalAntiAbout
	} from '../../api/index.js'
	export default {
		data() {
			return {
				errorType: ['toast'],
				params: {
					year: true,
					month: true,
					day: true,
					hour: true,
					minute: true,
					second: true
				},
				show: 1,
				timeshow: false,
				timeshow1: false,
				susheArr: [],
				sushe_show: false,
				action: 'http://www.example.com/upload',
				fileList: [],
				form: {
					universal_anti_about_workList: [{
						activitiyName: '',
						universalAntiAboutWorkMain18: '',
						specificActivities: ''
					}],
					universal_anti_about_fundsList: [{
						project: '',
						cost: ''
					}],
				},
				rules: {
					universalAntiAboutMain26: [{
						required: true,
						message: '请输入活动名称',
						trigger: ['change', 'blur'],
					}],
					title: [{
						required: true,
						message: '请输入活动标语',
						trigger: ['change', 'blur'],
					}],
					/*startDate: [{
						required: true,
						message: '请输入活动时间',
						trigger: ['change', 'blur'],
					}],*/
					address: [{
						required: true,
						message: '请输入活动地点',
						trigger: ['change', 'blur'],
					}],
					numberPeople: [{
						required: true,
						message: '请输入招募人数',
						trigger: ['change', 'blur'],
					}],
					/*deadlineDate: [{
						required: true,
						message: '请输入报名截止时间',
						trigger: ['change', 'blur'],
					}],*/
					activityContent: [{
						required: true,
						message: '请输入活动内容',
						trigger: ['change', 'blur'],
					}],
				}
			}
		},
		onLoad() {

		},
		onReady() {
			this.$refs.uForm.setRules(this.rules);
		},
		methods: {
			time_confirm(e) {
				console.log("ddfdf", e);
				this.form.startDate = e.year + '-' + e.month + '-' + e.day + ' ' + e.hour +
					':' + e.minute + ':' + e.second;
			},
			time_confirm1(e) {
				this.form.deadlineDate = e.year + '-' + e.month + '-' + e.day + ' ' + e.hour +
					':' + e.minute + ':' + e.second;
			},
			submit() {
				this.$refs.uForm.validate(valid => {
					if (valid) {
						MyUniversalAntiAbout(this.form).then(res => {
							if (res.code == 200) {
								this.$refs.uToast.show({
									title: '活动申请成功'
								});
								uni.navigateTo({
									url: '../my/initiate'
								})
							}
						});
					}
				});
			}
		}
	}
</script>

<style scoped>
	.register {
		background-color: #F5F6FA;
	}

	.register .title {
		width: 100%;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		padding: 32rpx 0 25rpx 0;
	}

	.topbox {
		width: 100%;
		padding: 64rpx 95rpx 100rpx 95rpx;
		position: relative;
	}

	.txt01 {
		position: absolute;
		top: 68rpx;
		left: 42rpx;
		width: 120rpx;
		text-align: center;
		z-index: 999;
	}

	.nomor {
		font-size: 24rpx;
		color: #999;
	}

	.nogray {
		font-size: 32rpx;
		color: #999;
	}

	.actblue {
		color: #296DCB;
	}

	.txt02 {
		left: 50%;
		margin-left: -60rpx;
	}

	.txt03 {
		position: absolute;
		top: 68rpx;
		width: 120rpx;
		text-align: center;
		right: 42rpx;
	}

	.mybox {
		text-align: center;
	}

	.mybox /deep/ .u-upload {
		justify-content: center;
	}

	.formBox {
		padding: 0 30rpx 30rpx 30rpx;
		background-color: #fff;
	}

	.mititle {
		font-size: 30rpx;
		font-weight: bold;
		color: #333333;
		height: 90rpx;
		line-height: 90rpx;
		margin: 0 -30rpx;
		padding: 0 30rpx;
		border-bottom: 1px solid #f1f1f1;
	}

	.smtitle {
		width: 100%;
		text-align: center;
		font-size: 24rpx;
		color: #B1B1B1;
	}

	.smtitle b {
		color: #FF9D1D;
	}
</style>